<template>
    <div>
        <el-dialog :close-on-click-modal="false" title="" :visible.sync="dialogShow" width="60%" @open="open"
            @close="exit">
            <el-col :span="24" class="dialogtitle" style="height:100px;margin:15px 0 0 0;">
                <!--    s_finish -->
                <el-col class="stepBox2">
                    <span class="stepLine s_finish"></span>
                    <div class="stepNum s_finish">
                        <p>1</p><span></span>
                    </div>
                    <span class="stepLine s_finish"></span>
                    <div v-bind:class="{ s_finish: activeStep>-1}" class="stepNum">
                        <p>2</p><span></span>
                    </div>
                    <span v-bind:class="{ s_finish: activeStep>-1}" class="stepLine"></span>
                    <div v-bind:class="{ s_finish: activeStep>0}" class="stepNum">
                        <p>3</p><span></span>
                    </div>
                    <span v-bind:class="{ s_finish: activeStep>0}" class="stepLine"></span>
                </el-col>
            </el-col>
            <el-col :span="24" style="margin-bottom: 2vh;height:2vh;background:#0f4c8b;"></el-col>
            <el-col :span="24" class="dialogbodySL">
                <div v-if="activeStep == -1" class="clear">
                    <el-table style="width: 100%" height="330px" :data="UntreatedProcess" id="tabl1">
                        <el-table-column label="" prop="opNo">
                        </el-table-column>
                        <el-table-column label="" prop="boxBarcode">
                        </el-table-column>
                        <el-table-column label="" prop="boxQuan">
                        </el-table-column>
                        <el-table-column label="" prop="createTime">
                        </el-table-column>
                        <el-table-column width="190" label="  ">
                            <template slot-scope="scope">
                                <el-button type="primary" size="mini" @click="$printer.printWipBox(scope.row)">
                                </el-button>
                                <el-button type="warning" size="mini" @click="machFeedingShow=true">
                                </el-button>
                            </template>
                        </el-table-column>
                    </el-table>
                </div>

                <div v-if="activeStep == 0" class="clear">
                    <el-table style="width: 100%" height="330px" :data="stockLogs" id="tabl2">
                        <el-table-column label="" prop="matCode"></el-table-column>
                        <el-table-column label="" prop="boxBarcode"></el-table-column>
                        <el-table-column label="" prop="matBatchNo"></el-table-column>
                        <el-table-column label="" prop="boxQuanLeft"></el-table-column>
                        <el-table-column width="90" label="">
                            <template slot-scope="scope">
                                <el-button type="warning" size="mini" @click="DeleteDialog(scope.$index)">
                                    
                                </el-button>
                            </template>
                        </el-table-column>
                    </el-table>
                </div>
                <div v-if="activeStep == 1">
                    <el-col :span="11">
                        <el-form ref="form" label-width="140px" @submit.native.prevent>
                            <el-form-item label="">
                                <el-input placeholder="" v-model="boxBarcodeInput"
                                    v-on:keyup.enter.native="onBoxBarcodeScan"></el-input>
                                <span v-if="errMsg.length>0" class="p_red">{{errMsg}}</span>
                            </el-form-item>
                        </el-form>
                    </el-col>
                    <el-col :span="2">
                        <el-form ref="form" label-width="40px">
                            <el-form-item>
                                <span>
                                    <img src="@/assets/images/sm.png" />
                                </span>
                            </el-form-item>
                        </el-form>
                    </el-col>

                    <el-col :span="24" style="position: relative;padding-bottom: 10px;">
                        <h3
                            style="position: absolute; top: 2vh; left: 5%; background: #12599A; color: rgb(253, 190, 69);font-size: 16px;">
                            </h3>
                        <p
                            style="height: 1px; float: left; margin-top: 3vh; width: 100%; border: 0.5px dashed rgb(253, 190, 69); box-sizing: border-box;">
                            &nbsp;</p>
                    </el-col>

                    <el-col :span="24" class="scanning">
                        <el-table style="width: 100%" height="230px" :data="cleanLogs"
                            :row-class-name="tableRowClassName">
                            <el-table-column label="" prop="matCode"></el-table-column>
                            <el-table-column label="" prop="boxBarcodeOld"></el-table-column>
                            <el-table-column label="" prop="clRestCount"></el-table-column>
                            <el-table-column label="" prop="boxBarcodeNew"></el-table-column>
                            <el-table-column label="">
                                <template slot-scope="scope">{{scope.row.isConfirmed ? '':''}}</template>
                            </el-table-column>
                            <el-table-column label="">
                                <template slot-scope="scope">
                                    <img src="@/assets/icon/refresh.png" />
                                    <span @click="rePrint(scope.row)"></span>
                                </template>
                            </el-table-column>
                        </el-table>
                    </el-col>
                </div>

                <div class="appdialog" style="float:right;padding: 10px;">
                    <span slot="footer" class="dialog-footer" v-if="activeStep == -1">
                        <el-button type="primary" @click="next" size="mini"></el-button>
                    </span>
                    <span slot="footer" class="dialog-footer" v-if="activeStep == 0">
                        <el-button type="warning" size="mini" @click="onNext"></el-button>
                    </span>
                    <span slot="footer" class="dialog-footer" v-if="activeStep == 1">
                        <el-button type="warning" size="mini" @click="onConfirm"></el-button>
                    </span>
                </div>
            </el-col>
        </el-dialog>
        <!--  -->
        <!---->
        <MachFeeding :show.sync="machFeedingShow" :ifInsert="true" :orders="orderArr"
            @exitmodal="queryUntreatedProcess(),machFeedingShow = false"></MachFeeding>
    </div>
</template>

<script>
    import MachFeeding from "@/components/mach_feeding.vue";
    export default {
        components: {
            MachFeeding: MachFeeding,
        },
        props: ["show", "order", "orderArr", "initCleanLogs", "skipPageOne"],
        name: 'OrderStockCline',
        data: function () {
            return {
                machFeedingShow: false,
                dialogShow: this.show,
                stockLogs: [],
                UntreatedProcess: [],//
                scanValue: "",
                errMsg: '',
                activeStep: -1,
                cleanLogs: [],
                boxBarcodeInput: '',
                ChoseReason: '',
                userInfo: [],
                ifUpdateBoxQuanLeft: false,//
            };
        },
        watch: {
            show: function (newValue, oldValue) {
                this.dialogShow = newValue;
            },
            dialogShow: function (newValue, oldValue) {
                if (this.show != this.dialogShow)
                    this.$emit("update:show", this.dialogShow);
            }
        },
        mounted: function () {

        },
        methods: {
            open: function () {
                this.ifUpdateBoxQuanLeft = false
                this.activeStep = -1,
                    setTimeout(() => {
                        this.queryUntreatedProcess()
                    }, 500);
            },
            onDialogOpen: function () {
                if (this.skipPageOne) {
                    this.onNext()
                }
                else {
                    this.activeStep = 0
                    var self = this

                    // 
                    this.$http.request({
                        url: '/api/po_stack/' + this.order.poCode,
                        method: 'GET',
                        params: {
                            is_cleaned: false
                        }
                    }).then(function (resp) {
                        if (resp.data instanceof Array) {
                            self.stockLogs = resp.data
                            //
                            if (self.stockLogs.length == 0) {
                                self.onSecondPage()
                            }
                        }
                    })
                }
            },
            //
            onNext: function () {
                if (this.ifUpdateBoxQuanLeft) {
                    // 
                    var self = this
                    this.$cardScan.auth(function (userInfo) {
                        if (userInfo) {
                            self.userInfo = userInfo
                            self.ifUpdateBoxQuanLeft = false
                            self.onNext()
                        }
                    })
                } else {
                    // 
                    var self = this
                    this.$http.request({
                        url: '/api/po_stack/' + this.order.poCode + '/clean',
                        method: 'POST',
                        showLoading: true,
                        params: {
                            confirmPersonName: this.userInfo.userName,
                            confirmPersonNumber: this.userInfo.userAccount
                        },
                        data: this.stockLogs
                    }).then(function (resp) {
                        if (resp.status == 100) {
                            self.onSecondPage()
                            self.errMsg = ''
                        } else {
                            self.$notify.error({
                                title: '',
                                message: resp.message
                            });
                        }
                    })
                }

            },
            onSecondPage: function () {
                var self = this;
                //
                this.$http.request({
                    url: '/api/po_stack/cleanlog?order_code=' + self.order.poCode + '&is_confirmed=false',
                    method: 'GET'
                }).then(function (resp) {
                    self.cleanLogs = resp.data.list;
                    if (self.cleanLogs.length > 0) {
                        for (var i = 0; i < self.cleanLogs.length; i++) {
                            self.$printer.printCleanLog(self.cleanLogs[i])
                        }
                    }
                    self.activeStep = 1
                })
            },
            onBoxBarcodeScan: function () {
                var boxBarcodeInputTmp = this.boxBarcodeInput
                this.boxBarcodeInput = ''

                var cleanlog = null
                for (var i = 0; i < this.cleanLogs.length; i++) {
                    if (this.cleanLogs[i].boxBarcodeNew == boxBarcodeInputTmp) {
                        cleanlog = this.cleanLogs[i]
                        break;
                    }
                }
                if (cleanlog == null) {
                    //
                    this.$notify.error({
                        title: '',
                        message: '!'
                    });
                } else {
                    var self = this
                    this.$http.request({
                        url: '/api/po_stack/clean_confirm/' + cleanlog.boxBarcodeNew,
                        method: 'POST'
                    }).then(function (resp) {
                        if (resp.status == 100) {
                            cleanlog.isConfirmed = true
                        } else {
                            self.errMsg = resp.message
                        }
                    })
                }

            },
            onConfirm: function () {
                this.$confirm('?', '', {
                    confirmButtonClass:'el-button--danger',
                    confirmButtonText: '',
                    cancelButtonClass: 'el-button--warning',
                    cancelButtonText: '',
                    type: 'warning'
                }).then(() => {
                    //
                    for (var i = 0; i < this.cleanLogs.length; i++) {
                        if (!this.cleanLogs[i].isConfirmed) {
                            this.$notify.error({
                                title: '',
                                message: '!'
                            });
                            return;
                        }
                    }
                    //
                    var self = this
                    this.$http.request({
                        url: '/api/order/' + this.order.poCode + '/close',
                        method: 'POST',
                        showLoading: true
                    }).then(function (resp) {
                        if (resp.status == 100) {
                            //
                            self.dialogShow = false
                            self.$emit("orderClosed")
                        } else {
                            self.$notify.error({
                                title: '',
                                message: resp.message
                            });
                        }
                    })
                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: ''
                    });
                });
            },
            rePrint: function (data) {
                this.$printer.printCleanLog(data)
            },

            tableRowClassName: function ({
                row,
                rowIndex
            }) {
                if (row.isConfirmed) {
                    return 'bgGreenRow';
                }
                return '';
            },
            exit() {
                this.$emit("orderClosed")
            },
            //
            queryUntreatedProcess: function () {
                var self = this
                this.$http.request({
                    url: '/api/package/boxlog',
                    method: 'GET',
                    showLoading: true,
                    params: {
                        line_id: this.order.lineId,
                        order_code: this.order.poCode,
                        is_wip: true,
                        is_feeded: false,
                    }
                }).then(function (resp) {
                    self.UntreatedProcess = resp.data.list
                    // for (var i = 0; i < resp.data.list.length; i++) {
                    //     if (resp.data.list[i].opNo != self.lastproc) {
                    //         self.UntreatedProcess[self.UntreatedProcess.length] = resp.data.list[i]
                    //     }
                    // }
                    if (self.UntreatedProcess == 0) {
                        self.onDialogOpen()
                    }
                })
            },
            // 
            next: function () {
                if (this.UntreatedProcess.length > 0) {
                    this.$message({
                        message: '',
                        type: 'warning'
                    });
                    return
                }
                this.onDialogOpen()
            },
            DeleteDialog: function (index) {
                this.$confirm('?', '', {
                    confirmButtonText: '',
                    cancelButtonText: '',
                    type: 'warning'
                }).then(() => {
                    this.stockLogs[index].boxQuanLeft = 0
                    this.ifUpdateBoxQuanLeft = true
                    this.$message('');
                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: ''
                    });
                });

            }

        }
    };
</script>


