<template>
  <div class="work">
    <el-row :gutter="10">
      <el-col :span="24">
        <div class="ButtonTitle">
          <el-col :span="24">
            <el-button type="primary" style="width:80px;" size="mini" @click="go"></el-button>
          </el-col>
        </div>
      </el-col>

      <el-col :span="24" class="whead">
        <el-col :span="6">
          <el-form ref="form" label-width="160px" style="margin-top:2vh;">
            <el-form-item label="">
              <el-input v-model="bianhao" placeholder=""></el-input>
            </el-form-item>
          </el-form>
        </el-col>

        <el-col :span="6">
          <el-form ref="form" label-width="160px" style="margin-top:2vh;">
            <el-form-item label="">
              <el-input v-model="jianma" placeholder=""></el-input>
            </el-form-item>
          </el-form>
        </el-col>

        <el-col :span="6">
          <el-form ref="form" label-width="160px" style="margin-top:2vh;">
            <el-form-item label="">
              <el-select v-model="value" clearable placeholder="" popper-class="select01">
                <el-option v-for="item in options" :key="item.value" :label="item.label" :value="item.value">
                </el-option>
              </el-select>
            </el-form-item>
          </el-form>
        </el-col>

        <el-col :span="6">
          <el-form ref="form" label-width="160px" style="margin-top:2vh;">
            <el-form-item>
              <el-button type="warning" size="mini" style="width:80px"></el-button>
            </el-form-item>
          </el-form>


        </el-col>
      </el-col>

      <el-col :span="24">
        <div>
            <p style="color:#DAA651;margin-top: 1vh;float:left" class="m_label"></p>
          </div>

          <el-col :span="24">
             <div class="qualified">
            <el-table  stripe style="width: 100%" height="725">
              <el-table-column  label="">
              </el-table-column>
              <el-table-column label="">
              </el-table-column>
              <el-table-column label="">
              </el-table-column>
              <el-table-column  label="">
              </el-table-column>
              <el-table-column  label="">
                <template>
                  <el-button type="primary" size="mini">  </el-button>
                </template>
              </el-table-column>
            </el-table>
            </div>
          </el-col>
      </el-col>

    </el-row>
  </div>
</template>
<script>
  export default {
    data() {
      return {
        bianhao: '',
        valuetime: '',
        jianma: '',
         options: [{
          value: '1',
          label: ''
        }, {
          value: '2',
          label: ''
        }, {
          value: '3',
          label: ''
        }, {
          value: '4',
          label: ''
        }, {
          value: '5',
          label: ''
        }],
        value: ''

      }
    },
    mounted: function () {
    },
    methods:{
        go(){
         this.$router.push({ path:'eprework_details'  })
      }
    }
  }

</script>
<style>

</style>



