<template>
    <div class="appzx">
        <el-row :gutter="10">
            <el-col style="text-align: right">
                <el-button type="primary" size="mini" @click="machFeedingShow=true"></el-button>
                <!-- <el-button type="warning" size="mini"></el-button> -->
            </el-col>
            <div class="content">
                <el-col :span="24">
                    <div class="perfromConLeft">
                        <div class="perfromConLeftTitle" style="position: relative;">
                            <el-col>
                                <h3 class="information">
                                    </h3>
                                <p class="line">
                                    &nbsp;</p>
                            </el-col>
                        </div>

                        <div class="perfromConLeftContent">
                            <el-col :span="5">
                                <el-form ref="form" label-width="120px">
                                    <el-form-item label="">
                                        {{$ls.get('factory').fcName}}
                                    </el-form-item>
                                </el-form>
                            </el-col>
                            <el-col :span="5">
                                <el-form ref="form" label-width="120px">
                                    <el-form-item label="">
                                        {{$ls.get('workshop').shopName}}
                                    </el-form-item>
                                </el-form>
                            </el-col>
                            <el-col :span="5">
                                <el-form ref="form" label-width="120px">
                                    <el-form-item label="">
                                        {{$ls.get('line').lineName}}<span style="color: rgb(228, 174, 94)">
                                            ()</span>
                                    </el-form-item>
                                </el-form>
                            </el-col>

                            <el-col :span="5">
                                <el-form ref="form" label-width="120px">
                                    <el-form-item label="">
                                        {{$ls.get('shift').sfName}}
                                    </el-form-item>
                                </el-form>
                            </el-col>

                            <el-col :span="4">
                                <el-form ref="form" label-width="120px">
                                    <el-form-item label="">
                                        {{$ls.get('opNo')}}
                                    </el-form-item>
                                </el-form>
                            </el-col>

                        </div>
                    </div>
                </el-col>
            </div>

            <el-col :span="10">
                <p style="margin-bottom: 1.2vh;height: 4vh;color:#DAA651;margin-top: 1vh;" class="m_label">
                </p>
                <div class="GDtable">
                    <!-- tbative = class -->
                    <div class="GDtables" v-for="data in orderArr" @click="changeOrder(data)"
                        v-bind:class="{ tbative: data.poCode==iftbative }">
                        <el-col :span="24" class="ative">
                            <p class="tbNative_p" style="margin-top: 1.5vh;margin-left: 2vh;">{{data.poCode}}
                                <span v-if="data.poStatus=='Executing'"></span>
                                <span v-else-if="data.poStatus=='Frozen'"></span>
                                <span v-else-if="data.poStatus=='Completed'"></span>
                                <span v-else-if="data.poStatus=='Assigned'"></span>
                                <span v-else>{{data.poStatus}}</span>
                            </p>
                            <p class="tbline">
                                &nbsp;</p>
                        </el-col>
                        <el-col :span="24">
                            <el-col :span="12">
                                <el-form ref="form">
                                    <el-form-item label="" style="margin-top: .5vh;margin-left: 2vh;">
                                        {{data.prodCode}}
                                    </el-form-item>
                                </el-form>
                            </el-col>
                            <el-col :span="12">
                                <el-form ref="form">
                                    <el-form-item label="" style="margin-top: .5vh;margin-left: 2vh;">
                                        {{data.prodTuhao}}
                                    </el-form-item>
                                </el-form>
                            </el-col>
                            <el-col :span=" 12">
                                <el-form ref="form">
                                    <!-- <el-form-item label="/" style="margin-top: .5vh;margin-left: 2vh;">
                                        <span class=" shiji">0</span>/{{data.poCount}}
                                    </el-form-item> -->
                                    <el-form-item label="" style="margin-top: .5vh;margin-left: 2vh;">
                                        {{data.poCount}}
                                    </el-form-item>
                                </el-form>
                            </el-col>
                            <el-col :span="12">
                                <el-form ref="form">
                                    <el-form-item label="" style="margin-top: .5vh;margin-left: 2vh;">
                                        {{data.poStartDatetime}}
                                    </el-form-item>
                                </el-form>
                            </el-col>
                        </el-col>
                        <el-col :span=" 24">
                            <el-form ref="form" label-width="80px">
                                <el-form-item style="float: right;margin-right: 4vh;">
                                    <el-button v-if="data.poStatus == 'Executing'" class="tbNative_b" type="primary"
                                        size="mini" @click="confirm(data)">
                                    </el-button>
                                    <el-button v-else class="tbNative_b" type="primary" size="mini"
                                        @click="startOrder(data)"></el-button>
                                </el-form-item>
                            </el-form>
                        </el-col>
                    </div>
                </div>
            </el-col>
            <el-col :span="14" class="tableBox">
                <p style="color:#DAA651;margin-top: 1vh;" class="m_label"></p>
                <div class="perfromConRight">
                    <div class="QDLTbale">
                        <el-col :span="24" class="QDLtbale" style="height: 7vh;border-bottom:1px solid #6780AA">
                            <el-form label-width="30px">
                                <el-col :span="6">
                                    <el-form-item>
                                        <span style="color:#D9A63E;font-weight: bold">{{iftbative}}</span>
                                    </el-form-item>
                                </el-col>
                                <el-col :span="6">
                                    <el-form-item>
                                        {{nowOrder.prodName}}
                                    </el-form-item>
                                </el-col>
                                <el-col :span="6">
                                    <el-form-item>
                                        {{nowOrder.touliaoSum}}
                                    </el-form-item>
                                </el-col>
                                <el-col :span="6">
                                    <el-form-item>
                                        {{nowOrder.xialiaoSum}}
                                    </el-form-item>
                                </el-col>
                            </el-form>
                        </el-col>
                        <el-col :span="24" class="QDLtbale" style="height: 65vh !important">
                            <el-table style="width: 100%;height: 60vh;overflow-y:scroll" :data="procList">
                                <el-table-column label="" prop="procCode">
                                </el-table-column>
                                <el-table-column label="" prop="procName">
                                </el-table-column>
                                <el-table-column label="" prop="prodInfo.prodCode">
                                </el-table-column>
                                <el-table-column label="" prop="prodInfo.opUpStockNum">
                                </el-table-column>
                                <el-table-column label="" prop="prodInfo.opSideStockNum">
                                </el-table-column>
                                <el-table-column label="" prop="prodInfo.unqualifyNum">
                                </el-table-column>
                                <el-table-column width="190" label="">
                                    <template slot-scope="scope">
                                        <el-button type="primary" size="mini" @click="lookInfo(scope.row)">
                                        </el-button>
                                        <el-button type="warning" size="mini" @click="unqualified(scope.row)">
                                        </el-button>
                                    </template>
                                </el-table-column>
                            </el-table>
                        </el-col>
                    </div>
                </div>
            </el-col>
        </el-row>
        <!--  -->
        <MachFeeding :show.sync="machFeedingShow" :orders="orderArr" :ifInsert="false" @exitmodal="exitmodal">
        </MachFeeding>
    </div>
</template>

<script>
    import MachFeeding from "@/components/mach_feeding.vue";
    export default {
        components: {
            MachFeeding: MachFeeding,
        },
        data() {
            return {
                iftbative: '',//
                nowOrder: '',
                orderArr: [],
                procList: [],
                machFeedingShow: false,//
            };
        },
        activated: function () {
            this.exitmodal()
        },
        watch: {
            '$route'() {
                if (this.$router.history.current.name == 'ProcExecution') {
                    this.exitmodal()
                }
            }
        },
        methods: {
            //
            changeOrder(data) {
                this.nowOrder=data
                this.iftbative = data.poCode//
                var self = this
                this.$http.request({
                    url: '/api/process_info/' + data.prodCode,
                    method: 'GET',
                    params: {
                        line_id: data.nowLineId
                    }
                }).then(function (resp) {
                    if (resp.status == 100) {
                        self.procList = resp.data
                        self.queryProcess(data)
                        self.queryStock(self.procList[0].procCode)//
                        self.queryProcPackage(self.procList[self.procList.length - 1].procCode)//
                        document.getElementById(self.iftbative).scrollIntoView({ behavior: "smooth" });
                    } else {
                        self.$notify.error({
                            title: '',
                            message: resp.message
                        });
                    }
                })
            },
            //
            queryStock: function (procCode) {
                var self = this;
                this.$http.request({
                    url: '/api/proc_stock/' + self.nowOrder.poCode + '/' + self.nowOrder.lineId + '/' + procCode,
                    method: 'GET',
                }).then(function (resp) {
                    self.nowOrder.touliaoSum = 0
                    for (var i = 0; i < resp.data.length; i++) {
                        self.nowOrder.touliaoSum += resp.data[i].boxQuan
                    }
                })
            },
            //
            queryProcPackage: function (procCode) {
                var self = this
                this.$http.request({
                    url: '/api/package/boxlog',
                    method: 'GET',
                    params: {
                        line_id: this.nowOrder.lineId,
                        order_code: this.nowOrder.poCode,
                        op_code: procCode,
                    }
                }).then(function (resp) {
                    if (resp.status == 100) {
                        self.nowOrder.xialiaoSum = 0
                        for (var i = 0; i < resp.data.list.length; i++) {
                            self.nowOrder.xialiaoSum += resp.data.list[i].boxQuan
                        }
                    }
                })
            },
            queryProcess(data) {
                var self = this
                var lineId = data.nowLineId
                for (var i = 0; i < self.procList.length; i++) {
                    (function (index) {
                        self.$http.request({
                            url: '/api/prod_status/process/' + data.poCode + '/' + lineId + '/' + self.procList[index].procCode,
                            method: 'GET',
                        }).then(function (resp) {
                            if (resp.status == 100) {
                                self.procList[index]['prodInfo'] = resp.data
                                self.$set(self.procList, index, self.procList[index])
                            } else {
                                self.$notify.error({
                                    title: '',
                                    message: resp.message
                                });
                            }
                        })
                    })(i);
                }
            },
            confirm(data){
                //
                this.$message({
                    message: '',
                    type: 'info'
                });
                // this.curtOrder = data;
                // this.cardScanShow = true
            },
            //model
            exitmodal(nowOrder, returnPkg) {
                this.machFeedingShow = false;
                if (nowOrder) {
                    nowOrder.nowLineId = returnPkg.lineId
                    this.nowOrder = nowOrder
                    this.iftbative = nowOrder.poCode
                    for (var i = 0; i < this.orderArr.length; i++) {
                        if (this.orderArr[i].poCode == this.nowOrder.poCode) {
                            this.changeOrder(nowOrder)
                            return
                        }
                    }
                    this.orderArr[this.orderArr.length] = this.nowOrder
                    this.changeOrder(this.nowOrder)
                } else {
                    if (!this.nowOrder)
                        return
                    this.changeOrder(this.nowOrder)
                }

            },
            // 
            lookInfo(info) {
                this.$router.push({
                    name: 'ProcProcessDetail',
                    query: {
                        procCode: info.procCode,
                        procName: info.procName,
                        poCode: this.iftbative,
                        prodCode: this.nowOrder.prodCode,
                        lineId: this.nowOrder.nowLineId
                    }
                })
            },
            //
            unqualified: function (data) {
                this.$router.push({
                    name: 'ProcUnqualified_details',
                    params: {
                        lineId: this.nowOrder.lineId,
                        order: this.nowOrder,
                        poCode: this.iftbative,
                        procCode: data.procCode,
                        procName: data.procName
                    }
                })
            }
        }
    }
</script>


