<template>
	<div class="app3">
		<el-row :gutter="10">
			<el-col :span="24">
				<div class="ButtonTitle">
					<el-col :lg="18" :xl="18">
						<el-button :type="iftbative? 'primary':'info'" style="width:70px;" size="mini"
							@click="OrderFeeding">
							
						</el-button>
						<el-button :type="iftbative? 'primary':'info'" style="width:70px;" size="mini"
							@click="cleanLine">
							
						</el-button>
						<el-button :type="iftbative? 'primary':'info'" style="width:70px;" size="mini" @click="pkgManu">
							
						</el-button>
					</el-col>
				</div>
			</el-col>
			<div class="content">
				<el-col :span="24">
					<div class="perfromConLeft">
						<div class="perfromConLeftTitle" style="position: relative;">
							<el-col :span="24">
								<h3 class="information"></h3>
								<p class="line">&nbsp;</p>
							</el-col>
						</div>
						<div class="perfromConLeftContent">
							<el-col :span="6">
								<el-form label-width="80px" style="margin-top: 1vh;margin-left: 4.5vh;">
									<el-form-item label="">{{$ls.get('factory').fcName}}</el-form-item>
								</el-form>
							</el-col>
							<el-col :span="6">
								<el-form label-width="80px" style="margin-top: 1vh;margin-left: 4.5vh;">
									<el-form-item label="">{{$ls.get('workshop').shopName}}</el-form-item>
								</el-form>
							</el-col>
							<el-col :span="6">
								<el-form label-width="80px" style="margin-top: 1vh;">
									<el-form-item label="">
										{{$ls.get('line').lineName}}
									</el-form-item>
								</el-form>
							</el-col>
							<el-col :span="6" style="text-align: right;">
								<el-form label-width="80px" style="margin-right: 3vh;">
									<el-form-item>
										<el-button :type="iftbative? 'warning':'info'" size="small"
											@click="errorProofing"></el-button>
									</el-form-item>
								</el-form>
							</el-col>
						</div>
					</div>
				</el-col>
			</div>
			<el-col :span="10">
				<p style="margin-bottom: 1.2vh;height: 4vh;color:#DAA651;margin-top: 1vh;" class="m_label">
					<el-date-picker class="datepicker" :clearable="false" v-model="orderDateTime" type="date"
						placeholder="" @change="queryOrder" format="yyyy  MM  dd " value-format="yyyy-MM-dd"
						:picker-options="pickerOptions">
					</el-date-picker>
				</p>
				<div class="GDtable" id="orderListBox">
					<!-- tbative = class -->
					<div class="GDtables" v-for="data in orderArr" :id="data.poCode" @click="changeOrder(data)"
						v-bind:class="{ tbative: data.poCode==iftbative }">
						<el-col :span="24" class="ative">
							<p class="tbNative_p" style="margin-top: 1.5vh;margin-left: 2vh;">{{data.poCode}}
								<span v-if="data.poStatus=='Executing'"></span>
								<span v-else-if="data.poStatus=='Frozen'"></span>
								<span v-else-if="data.poStatus=='Completed'"></span>
								<span v-else-if="data.poStatus=='Assigned'"></span>
								<span v-else>{{data.poStatus}}</span>
							</p>
							<p class="tbline">
								&nbsp;</p>
						</el-col>
						<el-col :span="24">
							<el-col :span="9">
								<el-form ref="form">
									<el-form-item label="" style="margin-top: .5vh;margin-left: 2vh;">
										{{data.prodCode}}
									</el-form-item>
								</el-form>
							</el-col>
							<el-col :span="15">
								<el-form ref="form">
									<el-form-item label="" style="margin-top: .5vh;margin-left: 2vh;">
										{{data.prodTuhao}}
									</el-form-item>
								</el-form>
							</el-col>
							<el-col :span="9">
								<el-form ref="form">
									<!-- <el-form-item label="/" style="margin-top: .5vh;margin-left: 2vh;">
                                        <span class=" shiji">0</span>/{{data.poCount}}
                                    </el-form-item> -->
									<el-form-item label="" style="margin-top: .5vh;margin-left: 2vh;">
										{{data.poCount}}
									</el-form-item>
								</el-form>
							</el-col>
							<el-col :span="15">
								<el-form ref="form">
									<el-form-item label="" style="margin-top: .5vh;margin-left: 2vh;">
										{{data.poStartDatetime}}
									</el-form-item>
								</el-form>
							</el-col>
						</el-col>
						<el-col :span=" 24">
							<el-form ref="form" label-width="80px">
								<el-form-item style="float: right;margin-right: 4vh;">
									<el-button v-if="data.poStatus == 'Frozen'" class="tbNative_b" size="mini"
										@click="thawModelShow=true" type="info">
									</el-button>
									<el-button v-if="data.poStatus == 'Executing'" class="tbNative_b" size="mini"
										@click="closeOrder(data)" type="info">
									</el-button>
									<el-button v-else class="tbNative_b" type="primary" size="mini"
										@click="startOrder(data)"></el-button>
								</el-form-item>
							</el-form>
						</el-col>
					</div>
				</div>
			</el-col>
			<el-col :span="14" class="tableBox">
				<p style="color:#DAA651;margin-top: 1vh;"></p>
				<div class="perfromConRight">
					<el-col :span="24" class="shebei">
						<el-col :span="24">
							<el-col :span="24">
								<el-col :span="24">
									<el-col :span="8">
										<el-form label-width="80px">
											<el-form-item label="">{{nowOrder.prodCode}}</el-form-item>
										</el-form>
									</el-col>
									<el-col :span="10">
										<el-form label-width="80px">
											<el-form-item label="">
												{{nowOrder.prodTuhao}}
											</el-form-item>
										</el-form>
									</el-col>
									<el-col :span="6">
										<el-form label-width="80px">
											<el-form-item label="">
												{{nowOrder.prodName}}</el-form-item>
										</el-form>
									</el-col>
								</el-col>
							</el-col>
						</el-col>
					</el-col>

					<div class="QDLTbale">
						<el-col :span="24" class="QDLtbale">
							<p style="margin-bottom: 0.5vh;margin-left:1vh;"></p>
							<el-table style="width: 100%;height: 25vh;overflow-y: scroll;" :data="boxlogArr">
								<el-table-column label="" prop="boxBarcode"></el-table-column>
								<el-table-column label="" prop="boxQuan"></el-table-column>
								<el-table-column label="" prop="createTime"></el-table-column>
							</el-table>
							<p style="margin-bottom: 0.5vh;margin-left:1vh;margin-top: 1vh;"></p>
							<el-table style="width: 100%;height:26vh;overflow-y: scroll;" :data="stockBomArr"
								:default-sort="{prop: 'materialNumLack', order: 'descending'}"
								:row-class-name="tableRowClassName">
								<el-table-column label="" prop="materialCode"></el-table-column>
								<el-table-column label="" prop="materialName"></el-table-column>
								<el-table-column label="" prop="materialNumLeft"></el-table-column>
								<el-table-column label="" prop="materialNumLack"></el-table-column>
							</el-table>
						</el-col>
					</div>
				</div>
			</el-col>
		</el-row>
		<!---->
		<PreparationVerify :show.sync="verifyShow" :order="nowOrder" @pass=""></PreparationVerify>
		<!--  -->
		<OrderFeeding :show.sync="orderFeedingShow" :order="nowOrder" @feeded="changeOrder(nowOrder)"></OrderFeeding>
		<!-- -->
		<OrderStockCline :show.sync="orderClineShow" :order="nowOrder" @orderClosed="queryOrder(nowOrder)">
		</OrderStockCline>
		<!-- -->
		<OrderPackageManu :show.sync="orderPkgManuShow" :order="nowOrder" stockResource="ORDER_SUB"
			:boxLog="boxlogToConfirm" :isMatBox="true" @packageDone="changeOrder(nowOrder)">
		</OrderPackageManu>
		<!--  -->
		<closeModel :show.sync="cardScanShow" @pass="cardCallBack"></closeModel>
		<!---->
		<thawModel :show.sync="thawModelShow" @pass="thawExit"></thawModel>


	</div>
</template>

<script>
	import thawModel from "@/components/card_input.vue";
	import closeModel from "@/components/card_input.vue";
	import OrderFeeding from "@/components/order_feeding.vue";
	import PreparationVerify from "@/components/preparation_verify.vue";
	import OrderStockCline from "@/components/order_stock_cline.vue";
	import OrderPackageManu from "@/components/order_package_manu.vue"
	export default {
		components: {
			thawModel,
			closeModel,
			OrderFeeding,
			PreparationVerify,
			OrderStockCline,
			OrderPackageManu
		},
		data() {
			return {
				isBegin: false,//
				cleanlogs: [],//
				boxlogToConfirm: '',//
				//model
				thawModelShow: false,//
				verifyShow: false,//
				orderFeedingShow: false,//
				orderClineShow: false,//
				orderPkgManuShow: false,//
				cardScanShow: false,//
				//APIdata
				orderArr: [],//
				boxlogArr: [],//
				stockBomArr: [],//x
				iftbative: '',//
				nowOrder: '',//

				lineId: this.$ls.get('line').lineId,
				orderDateTime: '',//
				pickerOptions: {
					shortcuts: [{
						text: '',
						onClick(picker) {
							picker.$emit('pick', new Date());
						}
					}, {
						text: '',
						onClick(picker) {
							const date = new Date();
							date.setTime(date.getTime() - 3600 * 1000 * 24);
							picker.$emit('pick', date);
						}
					}, {
						text: '',
						onClick(picker) {
							const date = new Date();
							date.setTime(date.getTime() + 3600 * 1000 * 24);
							picker.$emit('pick', date);
						}
					}, {
						text: '',
						onClick(picker) {
							const date = new Date();
							date.setTime(0);
							picker.$emit('pick', date);
						}
					}]
				},

			};
		},
		mounted: function () {
			this.queryOrder()
		},
		methods: {
			//
			queryOrder: function () {
				var start_time
				var end_time
				if (this.orderDateTime && this.orderDateTime != '1970-01-01') {
					start_time = this.orderDateTime + ' 00:00:00'
					end_time = this.orderDateTime + ' 23:59:59'
				} else {
					this.orderDateTime = null
					start_time = null
					end_time = null
				}
				//
				var self = this
				this.$http.request({
					url: '/api/order',
					method: 'GET',
					params: {
						line_id: this.lineId,
						status: JSON.stringify(['Executing', 'Assigned','Frozen']),
						start_time: start_time,
						end_time: end_time
					}
				}).then(function (resp) {
					self.orderArr = resp.data.list;
					self.ifhavaOrderTbative()
				});
			},
			//
			ifhavaOrderTbative() {
				if (this.iftbative) {
					for (var i = 0; i < this.orderArr.length; i++) {
						if (this.orderArr[i].poCode == this.iftbative) {
							return
						}
					}
				}
				this.iftbative = ''
				this.nowOrder = []
				this.boxlogArr = []
				this.stockBomArr = []
			},

			//
			changeOrder: function (data) {
				this.iftbative = data.poCode
				this.nowOrder = data
				document.getElementById(this.iftbative).scrollIntoView({ behavior: "smooth" });
				this.queryboxlog()//
				this.queryOrderStockBomView()//
				this.queryCleanlog()//
				this.queryboxlog()//
			},

			//
			queryboxlog: function () {
				var self = this
				this.$http.request({
					url: '/api/package/boxlog',
					method: 'GET',
					params: {
						order_code: this.nowOrder.poCode
					}
				}).then(function (resp) {
					self.boxlogArr = resp.data.list
					//
					for (var i = 0; i < self.boxlogArr.length; i++) {
						if (!self.boxlogArr[i].isConfirmed) {
							self.boxlogToConfirm = self.boxlogArr[i];
							self.orderPkgManuShow = true
							return;
						}
					}
					self.boxlogToConfirm = null
				});
			},
			//
			queryOrderStockBomView: function () {
				var self = this
				this.$http.request({
					url: '/api/po_stack/statistic/' + this.nowOrder.poCode,
					method: 'GET'
				}).then(function (resp) {
					if (resp.data) {
						self.stockBomArr = resp.data
					}
				})
			},
			//
			errorProofing: function () {
				if (!this.iftbative) {
					this.$message('!');
					return
				}
				this.verifyShow = true
			},

			//
			OrderFeeding: function () {
				if (!this.iftbative) {
					this.$message('!');
					return
				}
				this.orderFeedingShow = true
			},

			//
			queryCleanlog: function () {
				var self = this;
				//
				this.$http.request({
					url: '/api/po_stack/cleanlog?order_code=' + self.nowOrder.poCode + '&is_confirmed=false',
					method: 'GET'
				}).then(function (resp) {
					self.cleanlogs = resp.data.list;
					if (self.cleanlogs.length > 0) {
						self.orderClineShow = true;
					}
				})
			},
			//
			cleanLine: function () {
				if (!this.iftbative) {
					this.$message('!');
					return
				}
				this.orderClineShow = true
			},

			//
			pkgManu: function () {
				if (!this.iftbative) {
					this.$message('!');
					return
				}
				this.orderPkgManuShow = true
			},

			//
			startOrder: function (data) {
				var self = this
				this.$http.request({
					url: '/api/order/' + data.poCode + '/start',
					method: 'POST',
					params: {
						checkBoxInfo: false
					}
				}).then(function (resp) {
					if (resp.status == 100) {
						self.queryOrder();
					} else {
						self.$notify.error({
							title: '',
							message: resp.message
						});
					}
				})
			},
			//
			closeOrder: function (data) {
				//
				this.$message({
					message: '',
					type: 'info'
				});
				// this.cardScanShow = true
			},
			//
			cardCallBack: function (userData) {
				if (userData == null) {
					this.$notify.error({
						title: '',
						message: ''
					});
					return;
				}
				var self = this
				this.$http.request({
					url: '/api/order/' + this.nowOrder.poCode + '/close',
					method: 'POST',
					showLoading: true
				}).then(function (resp) {
					if (resp.status == 100) {
						self.$notify({
							title: '',
							message: '',
							type: 'success'
						});
					} else {
						self.$notify.error({
							title: '',
							message: resp.message
						});
					}
					self.queryOrder()
				})
			},
			//
			thawExit: function (userData) {
				var self = this;
				this.$http.request({
					url: "/api/order/" + this.nowOrder.poCode + "/unfreeze",
					method: "POST"
				}).then(function (resp) {
					if (resp.status != 100) {
						self.$notify.error({
							title: "",
							message:
								"" +
								self.nowOrder.poCode +
								"" +
								resp.message
						});
					} else {
						self.queryOrder();
						self.$notify({
							title: '',
							message: '',
							type: 'success'
						});
					}
				});
			},
            tableRowClassName: function ({
                row,
                rowIndex
            }) {
                if (row.materialNumLack > 0) {
                    return 'fontred01';
                }
                return '';
            },
		}
	};
</script>

<style>
</style>


