                            <el-col style="margin-top:.5vh;text-align:right">
                                <el-button disabled v-if="ifIsolation=='Isolation'" type="danger" size="small">
                                    
                                </el-button>
                                <el-button disabled v-if="ifIsolation=='Cancel'" type="danger" size="small">
                                    
                                </el-button>
                                <el-button v-if="ifIsolation!='Isolation' && ifIsolation!='Cancel'" type="danger"
                                    size="small" @click="DeleteMore">
                                    
                                </el-button>
                                <el-button style="float: left" v-if="ifIsolation!='Isolation' && ifIsolation!='Cancel'"
                                    type="warning" size="small" @click="queryMaterial">
                                    
                                </el-button>
