    </div>
</template>
<script>
    import inputCard from "@/components/card_input.vue";
    import moment from 'moment'
    export default {
        components: {
            inputCard: inputCard,
        },
        data() {
            return {
                // 
                rules: {
                    poCode: [
                        { required: true, message: '', trigger: 'change' }
                    ],
                    nplFindOp: [
                        { required: true, message: '', trigger: 'change' }
                    ],
                    nplProdOp: [
                        { required: true, message: '', trigger: 'change' }
                    ],
                    nplQty: [
                        { required: true, message: '', trigger: 'change' }
                    ],
                    nplIsTestPart: [
                        { required: true, message: '', trigger: 'change' }
                    ],
                    nplBatchStatus: [
                        { required: true, message: '', trigger: 'change' }
                    ],
                    nplDefect: [
                        { required: true, message: '', trigger: 'change' }
                    ],
                    createPersonName: [
                        { required: true, message: '', trigger: 'change' }
                    ]
                },
                tabsNum: 'message',//tabs
                MaterialEditBoxShow: false,//box
                tableIndex: '', // tableINdex
                ifcardShow: false,//
                ifcardShow2: false,//
                labelPosition: 'top', //css
                labelPosition1: 'right', //css
                nowLineId: this.$ls.get('line').lineId,//lineId
                //route
                ifIsolation: true,

                mainDatas: {
                    nonconformProductMaterialPOs: [],//
                    fcId: this.$ls.get('factory').fcId, // id
                    fcCode: this.$ls.get('factory').fcCode, // 
                    shopId: this.$ls.get('workshop').shopId, // ID
                    shopCode: this.$ls.get('workshop').shopName, // 
                    lineId: this.$ls.get('line').lineId, // ID
                    lineCode: this.$ls.get('line').lineName, // 	
                    shiftId: this.$ls.get('shift').sfId, // ID
                    shiftCode: this.$ls.get('shift').sfName, // 
                    prodDate: '',//
                    createTime: '',//
                    poCode: '',//
                    prodOrderCode: '',//
                    prodCode: '',//sap
                    prodNumber: '',//
                    prodTuhao: '',//
                    prodName: '',//
                    nplBarcode: '',//
                    ////
                    nplId: '',//id
                    nplProdOp: '',//
                    nplProdOpName: '',//
                    nplFindOp: '',//
                    nplFindOpName: '',//
                    ////
                    nplQty: '',//
                    nplIsTestPart: '',//
                    nplDutyType: '',//
                    nplBatchStatus: '', // 
                    nplDefect: '',//
                    nplDefectDesc: '',//
                    nplDeviceCode: '',//
                    createPersonName: '',//
                    createPersonNo: '',//id     null
                    ////
                    defectCollection: '',//
                    // nplStatus 
                    inputCardInfo: []
                },
                //
                orderArr: [],//
                ProcessArr: [],//
                DutyTypeArr: [],//
                BatchStatusArr: [],//
                TroubleArr: [],//
                //box
                edit_nplmBarcode: '',
                edit_materialName: '',
                edit_materialBoxCode: '',
                edit_materialBatchCode: '',
                edit_materialFurnaceNo: '',
                edit_materialCode: '',
                edit_materialQty: '',

                tableDeleteMore: [], //
            }
        },
        mounted() {
            this.queryTprDutyType()
            this.queryTprBatchStatus()
            this.queryTrouble()
            if (this.$route.params.ifIsolation) {
                // ||  || 
                this.ifIsolation = this.$route.params.ifIsolation
                this.mainDatas = this.$route.params.routerAlldatas
                this.queryProcess()
            } else {
                //
                this.ifIsolation = 'add'
                this.mainDatas.prodDate = moment().format("YYYY-MM-DD");
                this.mainDatas.createTime = moment().format("YYYY-MM-DD");
                this.mainDatas.createPersonName = this.$ls.get('user').userName
                if (this.$route.params.poCode) {
                    this.mainDatas.poCode = this.$route.params.poCode
                    this.mainDatas.nplFindOp = this.$route.params.procCode
                    this.mainDatas.nplFindOpName = this.$route.params.procName
                    if (this.$route.params.order) {
                        this.nowLineId = this.$route.params.lineId
                        this.mainDatas.lineId = this.$route.params.order.lineId, // ID
                            this.mainDatas.lineCode = '' // 	
                    }
                    this.queryOrder(this.$route.params.poCode)
                    return
                }
                this.queryOrder()
            }
        },

        methods: {
            //- 
            queryOrder: function (poCode) {
                var self = this
                this.$http.request({
                    url: '/api/order',
                    method: 'GET',
                    params: {
                        order_code: poCode,
                        line_id: this.nowLineId,
                        status: JSON.stringify(['Executing'])
                    }
                }).then(function (resp) {
                    if (resp) {
                        self.orderArr = resp.data.list;
                        if (self.ifIsolation == 'add') {
                            if (self.$route.params.poCode) {
                                if (self.orderArr.length < 1) {
                                    self.$notify.error({
                                        title: '',
                                        message: '  '
                                    })
                                }
                            } else {
                                self.mainDatas.poCode = self.orderArr[0].poCode
                            }
                            self.OrderChange(self.mainDatas.poCode)
                        }
                    } else {
                        self.$notify.error({
                            title: '',
                            message: ',' + ',' + resp.message
                        })
                    }
                })
            },
            //-
            OrderChange: function (value) {
                for (var i = 0; i < this.orderArr.length; i++) {
                    if (this.orderArr[i].poCode == value) {
                        this.mainDatas.prodCode = this.orderArr[i].prodCode
                        this.mainDatas.prodOrderCode = this.orderArr[i].prodCode
                        this.mainDatas.prodName = this.orderArr[i].prodName
                        this.mainDatas.prodTuhao = this.orderArr[i].prodTuhao
                        this.mainDatas.prodNumber = this.orderArr[i].prodNumber
                        this.queryProcess() // 
                        break;
                    }
                }
            },
            //
            saveDatas: function () {
                if (this.submitForm('mainDatas')) {
                    if (this.mainDatas.nonconformProductMaterialPOs.length == 0) {//
                        this.queryMaterial()
                        this.$message({
                            message: '!',
                            type: 'success'
                        });
                    } else {
                        if (!this.mainDatas.nplId) {
                            //
                            this.mainDatas.prodDate = moment().format("YYYY-MM-DD");
                            this.mainDatas.createTime = moment().format("YYYY-MM-DD");
                            this.inputRecord(false)
                        } else {
                            this.inputRecord(true)
                        }
                    }
                    this.tabsNum = 'loss'
                } else {
                    this.tabsNum = 'message'
                }
            },
            //
            queryProcess: function () {
                var self = this
                this.$http.request({
                    url: '/api/process_info/' + self.mainDatas.prodCode,
                    method: 'GET',
                    params: {
                        line_id: this.nowLineId,
                    }
                }).then(function (resp) {
                    //
                    if (resp) {
                        self.ProcessArr = resp.data
                    } else {
                        self.$notify.error({
                            title: '',
                            message: '' + resp.message
                        })
                    }
                })
            },
            //  
            queryMaterial: function () {
                var self = this
                this.$http.request({
                    url: '/api/nonconform_product/material/compute',
                    method: 'POST',
                    showLoading: true,
                    data: self.mainDatas
                }).then(function (resp) {
                    //
                    if (resp.status == 100) {
                        self.savaNum = false;
                        self.mainDatas.nonconformProductMaterialPOs = resp.data
                        self.reRecord()
                    } else {
                        self.$notify.error({
                            title: '',
                            message: ',' + ',' + resp.message
                        })
                    }
                })
            },
            // 
            inputRecord: function (ifadd) {
                var self = this
                this.$http.request({
                    url: '/api/nonconform_product/record',
                    method: 'POST',
                    showLoading: true,
                    data: self.mainDatas
                }).then(function (resp) {
                    //
                    if (resp.status == 100) {
                        if (!ifadd) {
                            self.$notify({
                                title: '',
                                message: '',
                                type: 'success',
                            });
                            self.mainDatas = resp.data
                        } else {
                            self.$notify({
                                title: '',
                                message: '',
                                type: 'success',
                            });
                        }
                        self.queryProduct() //
                    } else {
                        self.$notify.error({
                            title: '',
                            message: ',' + ',' + resp.message
                        })
                    }
                })
            },
            // 
            reRecord: function () {
                var self = this
                this.$http.request({
                    url: '/api/nonconform_product/record',
                    method: 'POST',
                    data: self.mainDatas
                }).then(function (resp) {
                    //
                    if (resp.status == 100) {
                        self.mainDatas = resp.data
                        self.$message({
                            message: '!',
                            type: 'success'
                        });
                        self.queryProduct() //
                    } else {
                        self.$notify.error({
                            title: '',
                            message: ',' + ',' + resp.message
                        })
                    }
                })
            },
            // 
            cardCallBack: function (userData) {
                if (userData != null) {
                    this.mainDatas.confirmPersonNo = userData.empNumber
                    this.mainDatas.confirmPersonName = userData.userName
                    this.monitorConfirm()
                }
                return;
            },
            // 
            cardCallBack2: function (userData) {
                if (userData != null) {
                    this.mainDatas.confirmPersonNo = userData.empNumber
                    this.mainDatas.confirmPersonName = userData.userName
                    this.monitorCancel()
                }
                return;
            },
            // 
            monitorCancel: function () {
                var self = this
                this.$http.request({
                    url: '/api/nonconform_product/record/cancel/' + self.mainDatas.nplId,
                    method: 'PUT',
                    showLoading: true,
                }).then(function (resp) {
                    //
                    if (resp.status == 100) {
                        self.queryProduct()
                        self.$notify({
                            title: '',
                            message: '',
                            type: 'success',
                        });
                    } else {
                        self.$notify.error({
                            title: '',
                            message: ',' + ',' + resp.message
                        })
                    }
                })
            },
            //&&
            ifmonitorConfirm: function (value) {
                if (this.mainDatas.nplId) {
                    if (value) {
                        this.ifcardShow = true
                    } else {
                        this.ifcardShow2 = true
                    }
                } else {
                    this.$message({
                        type: 'warning',
                        message: ''
                    });
                }
            },
            // 
            monitorConfirm: function () {
                var self = this
                this.$http.request({
                    url: '/api/nonconform_product/record/confirm',
                    method: 'POST',
                    showLoading: true,
                    data: self.mainDatas,
                    params: {
                        lineId: this.mainDatas.lineId,
                    }
                }).then(function (resp) {
                    //
                    if (resp.status == 100) {
                        self.queryProduct()
                        self.$notify({
                            title: '',
                            message: '',
                            type: 'success',
                        });
                    } else {
                        self.$notify.error({
                            title: '',
                            message: ',' + ',' + resp.message
                        })
                    }
                })
            },
            // 
            queryProduct: function () {
                var self = this
                this.$http.request({
                    url: '/api/nonconform_product/record/' + self.mainDatas.nplId,
                    method: 'GET',
                    showLoading: true,
                }).then(function (resp) {
                    if (resp.status == 100) {
                        self.mainDatas = resp.data
                        self.ifIsolation = resp.data.nplStatus
                    }
                })
            },
            //
            queryTprDutyType: function () {
                var self = this
                this.$http.request({
                    url: ' /api/dict_data/tpr_duty_type',
                    method: 'GET',
                }).then(function (resp) {
                    //
                    if (resp.status == 100) {
                        self.DutyTypeArr = resp.data
                    } else {
                        self.$notify.error({
                            title: '',
                            message: ',' + ',' + resp.message
                        })
                    }
                })
            },
            //
            queryTprBatchStatus: function () {
                var self = this
                this.$http.request({
                    url: '/api/dict_data/tpr_batch_status',
                    method: 'GET',
                }).then(function (resp) {
                    //
                    if (resp.status == 100) {
                        self.BatchStatusArr = resp.data
                    } else {
                        self.$notify.error({
                            title: '',
                            message: ',' + ',' + resp.message
                        })
                    }
                })
            },
            //
            queryTrouble: function () {
                var self = this
                this.$http.request({
                    url: '/api/nonconform_product/trouble_code/' + self.$ls.get('factory').fcSapCompany,
                    method: 'GET',
                }).then(function (resp) {
                    //
                    if (resp.status == 100) {
                        self.TroubleArr = resp.data
                    } else {
                        self.$notify.error({
                            title: '',
                            message: ',' + ',' + resp.message
                        })
                    }
                })
            },
            //&&
            getProcessName: function (value, ifFind) {
                for (var i = 0; i < this.ProcessArr.length; i++) {
                    if (this.ProcessArr[i].procCode == value) {
                        if (ifFind) {
                            this.mainDatas.nplFindOpName = this.ProcessArr[i].procName
                            break
                        } else {
                            this.mainDatas.nplProdOpName = this.ProcessArr[i].procName
                            break
                        }
                    }
                }
            },
            //
            getNplDefectDesc: function (value) {
                for (var i = 0; i < this.TroubleArr.length; i++) {
                    if (this.TroubleArr[i].trcTroubleCode == value) {
                        this.mainDatas.nplDefectDesc = this.TroubleArr[i].trcTroubleName
                        break
                    }
                }
            },
            // 
            submitForm(formName) {
                let r = false;
                this.$refs[formName].validate((valid) => {
                    if (valid) {
                        r = true
                    } else {
                        r = false
                    }
                });
                return r
            },
            // >table>
            editBox: function (index) {
                this.tableIndex = index // index
                // input....
                this.edit_nplmBarcode = this.mainDatas.nonconformProductMaterialPOs[index].nplmBarcode
                this.edit_materialName = this.mainDatas.nonconformProductMaterialPOs[index].materialName
                this.edit_materialBoxCode = this.mainDatas.nonconformProductMaterialPOs[index].materialBoxCode
                this.edit_materialBatchCode = this.mainDatas.nonconformProductMaterialPOs[index].materialBatchCode
                this.edit_materialFurnaceNo = this.mainDatas.nonconformProductMaterialPOs[index].materialFurnaceNo
                this.edit_materialCode = this.mainDatas.nonconformProductMaterialPOs[index].materialCode
                this.edit_materialQty = this.mainDatas.nonconformProductMaterialPOs[index].materialQty

            },
            // >table>>
            editboxConfirm: function () {
                // input...
                // this.mainDatas.nonconformProductMaterialPOs[this.tableIndex].nplBarcode = this.edit_materialName
                // this.mainDatas.nonconformProductMaterialPOs[this.tableIndex].materialBoxCode = this.edit_materialBoxCode
                // this.mainDatas.nonconformProductMaterialPOs[this.tableIndex].materialBatchCode = this.edit_materialBatchCode
                // this.mainDatas.nonconformProductMaterialPOs[this.tableIndex].materialFurnaceNo = this.edit_materialFurnaceNo
                // this.mainDatas.nonconformProductMaterialPOs[this.tableIndex].materialCode = this.edit_materialCode
                this.mainDatas.nonconformProductMaterialPOs[this.tableIndex].materialQty = this.edit_materialQty
                this.reRecord() //
            },
            //>
            Deletematerials: function (index) {
                if (this.mainDatas.nonconformProductMaterialPOs.length == 1) {//
                    this.$confirm('?', '', {
                        confirmButtonText: '',
                        cancelButtonText: '',
                        confirmButtonClass: 'el-button--danger',
                        type: 'warning'
                    }).then(() => {
                        this.ifcardShow2 = true
                    }).catch(() => {
                        this.$message({
                            type: 'info',
                            message: ''
                        });
                    });
                } else {
                    this.$confirm(', ?', '', {
                        confirmButtonText: '',
                        cancelButtonText: '',
                        confirmButtonClass: 'el-button--danger',
                        type: 'warning'
                    }).then(() => {
                        this.mainDatas.nonconformProductMaterialPOs.splice(index, 1)
                        this.reRecord()//
                    }).catch(() => {
                        this.$message({
                            type: 'info',
                            message: ''
                        });
                    });
                }

            },
            //
            DeleteMore: function () {
                if(this.tableDeleteMore.length == 0)
                    return
                this.$confirm(', ?', '', {
                    confirmButtonText: '',
                    cancelButtonText: '',
                    confirmButtonClass: 'el-button--danger',
                    type: 'warning'
                }).then(() => {
                    for (var i = 0; i < this.tableDeleteMore.length; i++) {
                        for (var t = 0; t < this.mainDatas.nonconformProductMaterialPOs.length; t++) {
                            if (this.tableDeleteMore[i].nplmId == this.mainDatas.nonconformProductMaterialPOs[t].nplmId) {
                                this.mainDatas.nonconformProductMaterialPOs.splice(t, 1)
                                continue;
                            }
                        }
                    }
                    this.reRecord()//
                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: ''
                    });
                });
            },
            handleSelectionChange(val) {
                this.tableDeleteMore = val
            },
            //
            getDefectCollection: function () {
                let Collection = this.mainDatas.defectCollection.split('-')
                this.mainDatas.nplFindOp = Collection[0]
                this.mainDatas.nplDefect = Collection[1]
                this.mainDatas.nplProdOp = Collection[2]
                this.getProcessName(this.mainDatas.nplProdOp, true)
                this.getProcessName(this.mainDatas.nplFindOp, false)
            },
            //
            Print: function () {
                var self = this;
                this.$printer.printNonconform(self.mainDatas);
            },

            //
            inputFocus: function () {
                this.mainDatas.createPersonName = '',
                    this.$message('')
            },
            onCardScan: function () {
                var qs = require('qs');
                var self = this
                if (!this.mainDatas.createPersonName) {
                    this.$notify.error({
                        title: '',
                        message: ''
                    });
                    return
                }
                this.$http.request({
                    url: '/api/user/card_login',
                    method: 'POST',
                    showLoading: true,
                    data: qs.stringify({ 'cardId': this.mainDatas.createPersonName })
                }).then(function (resp) {
                    if (resp.status == 100) {
                        self.mainDatas.createPersonName = resp.data.userName
                        self.$notify({
                            title: '',
                            message: '',
                            type: 'success'
                        });
                        self.$refs.inputer.blur()//
                    }
                    else {
                        self.mainDatas.createPersonName = ""
                        self.$notify.error({
                            title: '',
                            message: resp.message
                        });
                    }
                })
            }
        }
    }
</script>
<style>
    .materialPOs .el-checkbox__inner {
        width: 25px;
        height: 25px
    }

    .materialPOs .el-checkbox__inner::after {
        height: 15px;
        left: 8px;
        position: absolute;
        top: 1px;
        width: 6px;
    }

    .badproducts .badactive {
        height: 86vh;
        border: 1px solid #5a92c7;
        background-color: #0B3E7C;
        margin-top: 1vh;
    }

    .badproducts .ButtonTitle {
        top: 1vh;
    }

    .badproducts .badactive .el-tabs__item.is-active {
        color: #FFC44D;
    }

    .badproducts .badactive .el-tabs__active-bar {
        background-color: #FFC44D;
        left: 1vh;
    }

    .badproducts .badactive .el-tabs__item {
        color: #fff;
    }

    .badproducts .badactive .el-tabs__nav-wrap::after {
        background-color: #2F70BE;
    }

    .badproducts .badactive .el-tabs--bottom .el-tabs__item.is-bottom:nth-child(2),
    .el-tabs--bottom .el-tabs__item.is-top:nth-child(2),
    .el-tabs--top .el-tabs__item.is-bottom:nth-child(2),
    .el-tabs--top .el-tabs__item.is-top:nth-child(2) {
        padding-left: 2vh;
    }

    .badproducts .badactive .el-tabs--bottom .el-tabs__item.is-bottom:last-child,
    .el-tabs--bottom .el-tabs__item.is-top:last-child,
    .el-tabs--top .el-tabs__item.is-bottom:last-child,
    .el-tabs--top .el-tabs__item.is-top:last-child {
        margin-left: 1.2vh;
    }

    .badproducts .badactive .el-tabs__content {
        height: 80vh;
    }

    .badproducts .badactive .information {
        top: 0vh;
        color: #fff;
        font-size: 15px !important;

    }

    .badproducts .badactive .line {
        margin-top: 1vh;
        border: 0.5px dashed #fff;
    }

    .badproducts .badactive .el-form-item {
        margin-bottom: 0px;
    }

    .badproducts .badactive .el-select .el-input.is-focus .el-input__inner {
        border-color: #C0C4CC;
    }

    .badproducts .badactive .el-input__inner {
        background-color: transparent;
        color: #fff;
        height: 3.5vh !important;
    }

    .badproducts .badactive .el-textarea__inner {
        background-color: transparent;
        border-radius: 0px;
        max-width: 70vh;
    }

    .badproducts .badactive .el-select .el-input__inner:focus {
        border-color: #C0C4CC;
    }

    .badproducts .badactive .el-form-item__content {
        margin-left: 3.5vh;
    }

    .badproducts .badactive .input .el-input {
        width: 24vh;
    }

    .badproducts .badactive .input .el-form-item__content {
        margin-left: 3vh;
    }

    .badproducts .badactive .el-form--label-top .el-form-item__label {
        margin-left: 4.5vh;
        margin-top: 1vh;
        padding-bottom: 0px;
        padding-top: 0.5vh;
    }

    .badproducts .badactive .el-input {
        width: 24vh;
    }

    .badproducts .badactive .textuser .el-input__inner {
        color: #FFC44D;
    }

    .badproducts .badactive .shuoming {
        color: #DF434F;
        margin-top: 5vh;
    }

    .badproducts .el-table {
        border: 1px solid #5a92c7;

    }

    .badproducts .el-table th>.cell {
        background-color: #12599A;
        line-height: 4vh;
    }

    .badproducts .el-table td {
        border-bottom: 1px solid #2F70BE;
    }

    .badproducts .el-button--primary {
        color: #FFF;
        background-color: #2C77BC;
        border-color: #2C77BC;
    }

    .badproducts .el-table .el-button--mini {
        padding: 5px 20px;
    }

    .badproducts .el-dialog .el-input__inner {
        background-color: transparent;
        color: #fff;
        height: 3.5vh;
    }

    .badproducts .el-dialog .el-input {
        width: 70%;
    }

    .badproducts .el-dialog .el-form-item {
        margin-bottom: 22px;
        margin-top: 2vh;
    }

    .badproducts .el-dialog__footer {
        text-align: center;
        border-top: 1px solid #5a92c7;
    }

    .badproducts .badactive .inputsc .el-input {
        width: 100%;
    }

    .textarea_box textarea {
        color: #fff;
    }
</style>


